@import url('https://fonts.googleapis.com/css?family=Montserrat');
@import url('https://fonts.googleapis.com/css?family=Roboto+Mono');
@import url('https://fonts.googleapis.com/css?family=Bai+Jamjuree');

@font-face {
  font-family: 'StarJedi';
  src: url('./fonts/Starjedi.ttf') format('truetype');
}
@font-face {
  font-family: 'StarJediHollow';
  src: url('./fonts/Starjhol.ttf') format('truetype');
}
@font-face {
  font-family: 'StarJediOutline';
  src: url('./fonts/Starjout.ttf') format('truetype');
}

html, body, root {
    width: 100%;
}

#root {
    background-image: -webkit-gradient(linear, left top, left bottom, from(var(--background-color)), to(var( --background-color-dark)));
    background-image: -webkit-linear-gradient(top, var(--background-color), var( --background-color-dark));
    background-image: -o-linear-gradient(top, var(--background-color), var( --background-color-dark));
    background-image: linear-gradient(to bottom, var(--background-color), var( --background-color-dark));
}

* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family: 'Bai Jamjuree', sans-serif;
    font-size: 14px;

    --main-color: rgba(254, 219, 153, 1);
    --main-color-dark: rgb(41, 41, 41);
    --main-text-color: rgba(237, 234, 235, 1);
    --secondary-text-color: rgba(161, 155, 146, 0.9);
    --visited-color: rgba(170, 189, 181, 0.2);
    --main-color-vivid: rgb(245, 205, 58);
    --complementary-color: rgb(79, 186, 174);
    --complementary-color-transp: rgba(79, 186, 174, 0.1);
    --background-color: rgb(12, 100, 164);
    --background-color-dark: rgb(64, 66, 87);

    background-color: var(--background-color-dark);
    color: var(--main-text-color);

    padding: 0;
    margin: 0;
}

.App {
    width: 90%;
    margin: auto;
    background-color: white;
}

.content {
    min-height: calc(100vh - 50px);
}

i {
    background-color: transparent;
    margin-right: 10px;
    color: var(--complementary-color);
}

ul {
    list-style: none;
}

li {
    padding: 3px 0;
}

.main-inner{
  display: -ms-flexbox;
  display: flex;
}

@media screen and (max-width: 640px) {
    .App {
        width: 100%;
    }
}






