footer {
    height: 50px;
}

footer * {
    font-size: 12px;
    font-weight: 300;
}

.footer-inner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: end;
        align-items: end;

    height: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent) , to(var(--complementary-color-transp)) );
    background: -webkit-linear-gradient(top, transparent , var(--complementary-color-transp) );
    background: -o-linear-gradient(top, transparent , var(--complementary-color-transp) );
    background: linear-gradient(to bottom, transparent , var(--complementary-color-transp) );
}

.footer-inner:first-child {
    padding-left: 15px;
}

.footer-inner * {
    margin-top: 25px;
    margin-left: 5px;
}

.footer-inner a {
    padding: 0 5px;
    text-decoration: none;
    border-radius: 5px;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent) , to(var(--complementary-color)) );
    background: -webkit-linear-gradient(top, transparent , var(--complementary-color) );
    background: -o-linear-gradient(top, transparent , var(--complementary-color) );
    background: linear-gradient(to bottom, transparent , var(--complementary-color) );

}

@media (max-width:640px){
    .footer-push {
        height: 50px;
    }
}


