/*details tab*/

.details-container {
    display: inline-block;
    width: 0%;
    padding-left: 15px;
    margin-top: 10px;
}

.details-container.full-width {
    width: 100%;
    border-left: 2px solid var(--complementary-color);
}

.details__title {
    display: -ms-flexbox;
    display: flex;
    font-family: 'StarJedi', sans-serif;
    font-size: 20px;
    font-weight: 300;
    background-color: var(--main-color);
    color: var(--main-color-dark);
    border-radius: 5px 0px 0px 5px ;
    padding: 5px 0;
    padding-left: 15px;
    margin-bottom: 10px;
    position: relative;
}

.details__close-btn {
    position: relative;
    margin-left: auto;
    margin-right: 3vw;
    width: 80px;
    cursor: pointer;
    border: none;
    background: -webkit-gradient(linear, left top, right top, from(transparent), to(var(--complementary-color-transp)));
    background: -webkit-linear-gradient(left, transparent, var(--complementary-color-transp));
    background: -o-linear-gradient(left, transparent, var(--complementary-color-transp));
    background: linear-gradient(to right, transparent, var(--complementary-color-transp));
    border-radius: 15px;
    -webkit-transition: 0.54s;
    -o-transition: 0.54s;
    transition: 0.54s;
}
.details__close-btn:before {
    content: '';
    position: absolute;
    -webkit-transform: scale3d(0.5,0.5,0.5);
            transform: scale3d(0.5,0.5,0.5);
    top: -2px;
    right: 1vw;
    border-left: 18px solid var(--complementary-color);
    border-top: 18px solid transparent;
    border-bottom: 18px solid transparent;
}

.details__close-btn:hover {
    background: -webkit-gradient(linear, left top, right top, from(transparent), to(var(--complementary-color)));
    background: -webkit-linear-gradient(left, transparent, var(--complementary-color));
    background: -o-linear-gradient(left, transparent, var(--complementary-color));
    background: linear-gradient(to right, transparent, var(--complementary-color));
}

.details-container ul {
    padding-right: 20px;
}

li.details{
    position: relative;
    cursor: pointer;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.details-container li b {
    color: var(--complementary-color);
    cursor: default;
}

.details span, li a {
    margin: 0 3px;
    -webkit-transition: 0.1s ease-in-out;
    -o-transition: 0.1s ease-in-out;
    transition: 0.1s ease-in-out;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, transparent), to(var(--complementary-color-transp)) );
    background: -webkit-linear-gradient(top, transparent 50%, var(--complementary-color-transp) );
    background: -o-linear-gradient(top, transparent 50%, var(--complementary-color-transp) );
    background: linear-gradient(to bottom, transparent 50%, var(--complementary-color-transp) );
    border-radius: 5px;
    padding: 0 10px;
    padding-bottom: 4px;
}

.details span:hover, li a:hover {
    color: var(--main-color);
}

.details span:after {
    content: "•";
    position: relative;
    right: -3px;
    color: var(--main-color);
}

li a{
    text-decoration: none;
}

@media screen and (max-width: 640px) {

    .details-container * {
        font-size: 14px;
    }

    .details__title {
        font-size: 18px;
    }

    .details-container.full-width {
       margin-left: 10px;
    }
}





